html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}
 
html {
  font-size: 62.5%; 
} 


/* stripping of styles */
body,
ul,
h1,
h2,
h3
p {
margin: 0;
padding: 0;
}

body{
  /* background:#A3BCF9; */
  background: #8deed6;
}


/* a{
  text-decoration: none;
} */


img {
width: 80%;
height: auto;
display: block;
margin: 0 auto;
} 

.wrapper {
  width: 90%;
  max-width: 1480px;
  margin: 0 auto;
  }


.App {
  text-align: center;
}


.imageDisplay p{
  /* width:80%; */
  font-weight:300;
}



header{
  padding-top:100px;
  height:50vh;
  /* background:#D1D2F9; */
  font-family: mina, serif;
  font-weight: 400;
  font-style: normal;
}

header p, 
input,
select,
.options,
.submitButton,
p{
  font-family: professor, sans-serif;
  font-weight: 400;
  font-style: normal;
}

h1{
  font-size: calc(4.5rem + 1vw);
  font-weight:900;
}




main,
footer,
.reviewSection{
  padding:20px;
}



.imageDisplay{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 5px;
  grid-auto-rows: minmax(100px, auto);
  padding:20px;
  z-index:4;
}

.makeupItems{
  padding:10px;
  border: 2px solid pink;
  padding-bottom:50px;
  transition: all 0.3s ease;
}


p{
  font-size: calc(1.5rem + 1vw);
  margin-top:10px;
  color:black;
}



footer p{
  margin-top:100px;
}

h1{
  color:#ffffff;
}




select,
option,
input
{
  width:50%;
  text-align: center;
  margin:10px;
  background:pink;
  color:white;
  border: 5px solid pink;
}

select,
option{
  height:50px;
  font-size:3rem;
  text-transform: uppercase;

}


.submitButton{
  height:50px;
  border: 5px solid pink;
  font-size: 3.5rem;
  text-transform: uppercase;
  background:pink;
  color:white;
}





.imageDisplay a{
  text-align: center;
  font-size:1.7rem;
  margin:5px;
  padding:15px;
  background:pink;
  border:none;
  color:white;
  text-decoration: none;
  transition: all 0.2s ease-in;
  font-family: cardea-lining, serif;
  font-weight: 400;
  font-style: normal;  
}

.imageDisplay a:hover{
  background:#8deed6;
}

.imageDisplay a:active{
  background:#8deed6;
}

.makeupItems:hover{
  transform:scale(1.2);
  background-color:white;
}

.menu{
  top:0;
  margin:10px; 
  right:0;
  position: absolute;
  display:flex;
  border-bottom: 1px solid black;
}


.link{
  transition: all 1s ease-in;
}
.link:hover{
  background:linear-gradient(180deg,rgba(255,255,255,0) 50%, #FFD0AE 50%);
}

.blue {
  color: black;
  background: #8deed6;
  border:none;
  display: block;
  font-size: 20px;
  font-family: mina, serif;
  font-weight: 400;
  font-style: normal;
  transition: all 2s ease;
}

.blue:hover{
  background:linear-gradient(180deg,rgba(255,255,255,0) 50%, #FFD0AE 50%);
}



@media(max-width:860px){
  .imageDisplay a{
    text-align: center;
    font-size:1.5rem;
    margin-top:2px;
    padding:2px;
    color:white;
    text-decoration: none;
    display:flex;
    flex-direction:column;
  }
}

@media (max-width: 600px){
  .imageDisplay{
    grid-template-columns: repeat(3, 1fr);
  }

}



@media (max-width: 480px){
  .imageDisplay{
    grid-template-columns: repeat(2, 1fr);
  }

  .link{
    font-size:15px;
  }


}










